<template>
  <div class="course-img">
    <img :src="image" alt="cookImg" />
    <button type="button" v-if="isPlayButton" @click="playVR">
      <span>{{ $t('course-detail.VR-play-btn') }}</span>
      <font-awesome-icon :icon="castIcon" class="icon" />
    </button>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, SetupContext } from 'vue'
import { faChromecast } from '@fortawesome/free-brands-svg-icons'
import { useStore } from 'vuex'

export default defineComponent({
  props: {
    image: {
      type: String,
      required: true
    },
    isPlayButton: {
      type: Boolean,
      required: false,
      default: true
    },
    videoData: {
      type: Object
    }
  },
  emits: ['device-link-popup-active', 'set-modal-active'],
  setup(props, context: SetupContext) {
    const store = useStore()
    const connectingBluetoothDeviceMacAddress = computed(() => store.state.connectingBluetoothDevice.macAddress)
    const playVR = async () => {
      if (connectingBluetoothDeviceMacAddress.value) {
        const isBtResult = await store
          .dispatch('submitBTDeviceData', {
            videoData: JSON.stringify(props.videoData)
          })
          .then((result) => result)
        if (isBtResult === 'true') {
          context.emit('set-modal-active', true)
          return
        } else {
          //  TODO falseの処理
          console.log('false')
        }
      }
      context.emit('device-link-popup-active', true)
    }

    const castIcon = computed(() => faChromecast)

    return {
      castIcon,
      playVR
      // submitBTDeviceData
    }
  }
})
</script>

